import kotlinx.browser.window
import org.khronos.webgl.Uint8Array
import org.mider.code.MiderCodeParserConfiguration
import org.mider.code.produceCore
import org.mider.dsl.MiderDSL
import org.mider.dsl.dsl2MusicXml
import org.mider.dsl.fromDsl
import org.mider.dsl.fromDslInstance


fun genMidiBuffer(code: String, cfg: MiderCodeParserConfiguration = MiderCodeParserConfiguration()): Uint8Array {
    return fromDslInstance(produceCore(code, cfg).miderDSL).doFinal().prototype
}

fun genMusicXml(code: String, cfg: MiderCodeParserConfiguration = MiderCodeParserConfiguration(), divisions: Int = 480): String {
    return dsl2MusicXml(produceCore(code, cfg).miderDSL, divisions).toString()
}

fun main() {
    console.log("mider loaded!")
    val dw: dynamic = window
    dw.miderGenMidiBuffer = ::genMidiBuffer
    dw.miderGenMusicXml = ::genMusicXml
    dw.miderMiderCodeParserConfiguration = ::MiderCodeParserConfiguration
}